import React from 'react';
import './contents.css'
import video from "../utils/video.mp4"

const Services = ({
  title = 'helooo',
  content = ['one', 'two', 'three', 'four', 'five', 'six', 'seven'],
}) => {
  return (
    <div className='hoover'
      width="416px"
      style={{
        borderRadius: '12px',
        width: '314px',
        padding: '14px 4px',
        margin: '14px 0px',
      }}
    >

{/* <video  style={{width:"304px",zIndex:"-9"}} autoPlay loop muted className={{}}>
      <source src={video} type="video/mp4" />
      {/* Add additional source elements for other video formats */}
    {/* </video>  */}

      <div  style={{}}>
        <a href="" className='titleaa'>{title}</a>
      </div>
      <div style={{ display: 'flex', flexDirection: 'column'}}>
        {content.map((a) => (
          <a
            className='data'
            style={{ textDecoration: 'none', fontSize: '18px', margin:"7px"  }}
            href=""
          >
            {a}
          </a>
        ))}
      </div>
    </div>
  );
};

export default Services;
