import React from 'react';
import Layout from '../layout/layout';
import Content from '../components/contents';
import data from '../data/services';
import "./service.css"
 import { Seo } from "../components/seo";

// import Video from "../utils/video.mp4"
import Video from "../utils/2.mp4"

const Service = () => {
  // const he =data["Business Setup"].map((e,b)=>{(e,b)})
  // console.log(he)

  

  // r.map((a,b)=>{
  //   console.log(typeof a[0], a[1],...r)
  // })

  return (
    <Layout>
      <div  className='container2' style={{ display: "flex", justifyContent: "center" }}>

            <video
              style={{
                position: "absolute",
                zIndex: "-9",
                width: "600px",
                top: "150px",
                borderRadius:"30px"
              }}
              autoPlay
              loop
              muted
              className={{}}
            >
              <source src={Video} type="video/mp4" />
              {/* Add additional source elements for other video formats */}
            </video>
            <h2 className="heading " style={{padding:"90px 0px 0px 0px"}}>Services</h2>

          </div>

      {/* <section className=" banner-padding "  > */}
        {/* <div className="banner__background-wrap banner__content"> */}

        <div
          className=" wrap"
          style={{
           // flexWrap:"wrap",
            // border: '1px solid blue',
            display:"flex",
            // margin:"80px",
            fontSize:"30px",
            alignItems: 'flex-start',
            justifyContent: 'space-evenly',
            gap: '2% 2%',
            marginTop:"250px"
          }}
        >
          
          {data.map((a, b) => {
            console.log(a[0], a[1]);
            return( 
            <div style={{ flexBasis:"29%", }}>
             <Content title={a[0][0]} content={a[0].slice(1)} />
            <Content title={a[1][0]} content={a[1].slice(1)} />
              </div>
            )
          })}

          {/* <div className="row">
</div> */}
        </div>
        {/* </div> */}
      {/* </section> */}
    </Layout>
  );
};

export default Service;

export const Head = () => <Seo title="Service Details" />
